import React, { useState, useEffect } from 'react';
import {
  Container,
  Typography,
  Box,
  Paper,
  FormGroup,
  FormControlLabel,
  Checkbox,
  Button,
  Alert,
} from '@mui/material';
import { adminAPI } from '../services/api';
import { useError } from '../contexts/ErrorContext';
import { DayOfWeekSettings } from '../types';

const DAYS_OF_WEEK = [
  { key: 'monday', label: '' },
  { key: 'tuesday', label: '' },
  { key: 'wednesday', label: '' },
  { key: 'thursday', label: '' },
  { key: 'friday', label: '' },
  { key: 'saturday', label: '' },
  { key: 'sunday', label: '' },
] as const;

const AdminDayOfWeekSettingsPage: React.FC = () => {
  const { showError } = useError();
  const [settings, setSettings] = useState<DayOfWeekSettings>({
    monday: false,
    tuesday: false,
    wednesday: false,
    thursday: false,
    friday: false,
    saturday: false,
    sunday: false,
  });
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [successMessage, setSuccessMessage] = useState('');

  useEffect(() => {
    fetchSettings();
  }, []);

  const fetchSettings = async () => {
    try {
      const data = await adminAPI.getDayOfWeekSettings();
      setSettings(data);
    } catch (error) {
      showError('');
    } finally {
      setLoading(false);
    }
  };

  const handleChange = (day: keyof DayOfWeekSettings) => {
    setSettings(prev => ({
      ...prev,
      [day]: !prev[day],
    }));
  };

  const handleSave = async () => {
    setSaving(true);
    setSuccessMessage('');
    
    try {
      await adminAPI.updateDayOfWeekSettings(settings);
      setSuccessMessage('');
      setTimeout(() => setSuccessMessage(''), 3000); // 3
    } catch (error: any) {
      if (error.response?.data?.detail) {
        showError(error.response.data.detail);
      } else {
        showError('');
      }
    } finally {
      setSaving(false);
    }
  };

  if (loading) {
    return (
      <Container>
        <Box display="flex" justifyContent="center" alignItems="center" height="50vh">
          <Typography></Typography>
        </Box>
      </Container>
    );
  }

  return (
    <Container maxWidth="md">
      <Box sx={{ mt: 4, mb: 4 }}>
        <Typography variant="h4" component="h1" gutterBottom>
          
        </Typography>
        <Typography variant="body1" color="textSecondary" paragraph>
          
        </Typography>

        {successMessage && (
          <Alert severity="success" sx={{ mb: 3 }}>
            {successMessage}
          </Alert>
        )}

        <Paper elevation={3} sx={{ p: 4 }}>
          <Typography variant="h6" gutterBottom>
            
          </Typography>
          
          <FormGroup>
            {DAYS_OF_WEEK.map((day) => (
              <FormControlLabel
                key={day.key}
                control={
                  <Checkbox
                    checked={settings[day.key]}
                    onChange={() => handleChange(day.key)}
                    color="primary"
                  />
                }
                label={day.label}
                sx={{ mb: 1 }}
              />
            ))}
          </FormGroup>

          <Box sx={{ mt: 4, display: 'flex', justifyContent: 'center' }}>
            <Button
              variant="contained"
              size="large"
              onClick={handleSave}
              disabled={saving}
              sx={{ minWidth: 200 }}
            >
              {saving ? '...' : ''}
            </Button>
          </Box>

          <Box sx={{ mt: 2, p: 2, bgcolor: 'grey.100', borderRadius: 1 }}>
            <Typography variant="body2" color="textSecondary">
               <br/>
              
            </Typography>
          </Box>
        </Paper>
      </Box>
    </Container>
  );
};

export default AdminDayOfWeekSettingsPage;