import React, { useState, useEffect } from 'react';
import {
  Container,
  Typography,
  Box,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Button,
  Chip,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
} from '@mui/material';
import { DatePicker, TimePicker } from '@mui/x-date-pickers';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { format } from 'date-fns';
import { ja } from 'date-fns/locale';
import { adminAPI } from '../services/api';
import { useError } from '../contexts/ErrorContext';
import { ShiftRequest, ConfirmedShiftCreate } from '../types';

const AdminShiftRequestsPage: React.FC = () => {
  const { showError } = useError();
  const [requests, setRequests] = useState<ShiftRequest[]>([]);
  const [loading, setLoading] = useState(true);
  const [confirmDialog, setConfirmDialog] = useState<{
    open: boolean;
    request: ShiftRequest | null;
  }>({ open: false, request: null });
  const [confirmData, setConfirmData] = useState<{
    start_time: Date | null;
    end_time: Date | null;
  }>({ start_time: null, end_time: null });

  useEffect(() => {
    fetchShiftRequests();
  }, []);

  const fetchShiftRequests = async () => {
    try {
      const data = await adminAPI.getAllShiftRequests();
      setRequests(data);
    } catch (error) {
      showError('');
    } finally {
      setLoading(false);
    }
  };

  const openConfirmDialog = (request: ShiftRequest) => {
    setConfirmDialog({ open: true, request });
    // 
    setConfirmData({
      start_time: request.start_time ? new Date(request.start_time) : null,
      end_time: request.end_time ? new Date(request.end_time) : null,
    });
  };

  const handleConfirmShift = async () => {
    if (!confirmDialog.request || !confirmData.start_time || !confirmData.end_time) {
      showError('');
      return;
    }

    try {
      const shiftData: ConfirmedShiftCreate = {
        date: confirmDialog.request.date,
        start_time: confirmData.start_time.toISOString(),
        end_time: confirmData.end_time.toISOString(),
        user_id: confirmDialog.request.user_id,
      };

      await adminAPI.createConfirmedShift(shiftData);
      showError(''); // 
      setConfirmDialog({ open: false, request: null });
      // 
    } catch (error: any) {
      if (error.response?.data?.detail) {
        showError(error.response.data.detail);
      } else {
        showError('');
      }
    }
  };

  if (loading) {
    return (
      <Container>
        <Box display="flex" justifyContent="center" alignItems="center" height="50vh">
          <Typography></Typography>
        </Box>
      </Container>
    );
  }

  return (
    <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={ja}>
      <Container maxWidth="lg">
        <Box sx={{ mt: 4, mb: 4 }}>
          <Typography variant="h4" component="h1" gutterBottom>
            
          </Typography>

          <TableContainer component={Paper}>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {requests.map((request) => (
                  <TableRow key={request.id}>
                    <TableCell>
                      {format(new Date(request.date), 'Md(E)', { locale: ja })}
                    </TableCell>
                    <TableCell>{request.user_display_name || `${request.user_id}`}</TableCell>
                    <TableCell>
                      <Chip
                        label={request.canwork ? '' : ''}
                        color={request.canwork ? 'success' : 'error'}
                        size="small"
                      />
                    </TableCell>
                    <TableCell>
                      {request.canwork && request.start_time && request.end_time
                        ? `${format(new Date(request.start_time), 'HH:mm')} - ${format(new Date(request.end_time), 'HH:mm')}`
                        : '-'
                      }
                    </TableCell>
                    <TableCell>{request.description || '-'}</TableCell>
                    <TableCell>
                      {request.canwork && (
                        <Button
                          variant="contained"
                          size="small"
                          onClick={() => openConfirmDialog(request)}
                        >
                          
                        </Button>
                      )}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Box>

        {/*  */}
        <Dialog open={confirmDialog.open} onClose={() => setConfirmDialog({ open: false, request: null })}>
          <DialogTitle></DialogTitle>
          <DialogContent>
            <Box sx={{ pt: 2 }}>
              <Typography gutterBottom>
                {confirmDialog.request?.user_display_name}
                {confirmDialog.request && format(new Date(confirmDialog.request.date), 'Md(E)', { locale: ja })}
                
              </Typography>
              
              <TimePicker
                label=""
                value={confirmData.start_time}
                onChange={(newValue) => setConfirmData({ ...confirmData, start_time: newValue })}
                slots={{ textField: TextField }}
                slotProps={{
                  textField: {
                    fullWidth: true,
                    margin: 'normal',
                  },
                }}
              />

              <TimePicker
                label=""
                value={confirmData.end_time}
                onChange={(newValue) => setConfirmData({ ...confirmData, end_time: newValue })}
                slots={{ textField: TextField }}
                slotProps={{
                  textField: {
                    fullWidth: true,
                    margin: 'normal',
                  },
                }}
              />
            </Box>
          </DialogContent>
          <DialogActions>
            <Button onClick={() => setConfirmDialog({ open: false, request: null })}>
              
            </Button>
            <Button onClick={handleConfirmShift} variant="contained">
              
            </Button>
          </DialogActions>
        </Dialog>
      </Container>
    </LocalizationProvider>
  );
};

export default AdminShiftRequestsPage;